$font-primary: "coolvetica_rg";
$font-secondary: "Rubik", sans-serif;
$color-primary-dark: #61b8bb;
$color-secondary-dark: rgb(37, 37, 37);

@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;600&display=swap");

@font-face {
  font-family: "coolvetica_rg";
  src: url("../fonts/coolvetica_rg-webfont.woff") format("woff2"),
    url("../fonts/coolvetica_rg-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.projects--container {
  width: 66.67vw;
  margin: 0 auto;
  margin-top: 40px;
  height: 110vh;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  h1 {
    font-family: $font-primary;
    letter-spacing: 0.1rem;
    font-size: 2rem;
    font-weight: 400;
    padding: 10px;
    margin-bottom: 20px;
  }
}

.rows {
  flex-direction: row-reverse;
}

